import React, { useState, useEffect } from "react";
import "react-datepicker/dist/react-datepicker.css";
import TopBar from "../../components/Users/TopBar";
import "bootstrap/dist/css/bootstrap.min.css";
import { Check } from "lucide-react";
import {
  SectionWrapper,
  Wrapper,
} from "../../components/Styles/RegisterStyles";
import "react-big-calendar/lib/css/react-big-calendar.css";
import ModuleSideNav from "../../components/Users/ModuleSideNav";
import axios from "axios";

export default function SubscriptionUser() {
  const [isCollapsed, setIsCollapsed] = useState(false);

  return (
    <>
      <Wrapper>
        <div className="fullpage d-block">
          <div className="d-flex align-items-start gap-0">
            <ModuleSideNav
              isCollapsed={isCollapsed}
              setIsCollapsed={setIsCollapsed}
            />
            <div
              className={`global_view ${isCollapsed ? "global_view_col" : ""}`}
            >
              <TopBar />
              <SectionWrapper className="d-block p-md-4 p-3">
                <div className="container-fluid">
                  <div className="subscription-header">
                    <div className="subscription-title">
                      <h1>Your Package Subscriptions</h1>
                      <p>Manage your active plans and services</p>
                    </div>
                  </div>
                  <div className="row gy-5 py-3">
                    <div className="col-md-4">
                      <div className="package_card">
                        <div className="d-flex flex-column gap-0">
                          <div className="d-flex flex-column gap-2 card_top">
                            <h3>Investor Ops</h3>
                            <ul className="d-flex flex-column gap-1">
                              <li>€260 Euro annual.</li>
                              <li>Unlimited stakeholders</li>
                              <li>Start unlimited investment rounds</li>
                              <li>Unlimited investor reports</li>
                              <li>Facilitate the close of rounds</li>
                            </ul>
                          </div>
                          <div className="inner_card d-flex flex-column gap-2">
                            <span className="mainp text-white">Includes :</span>
                            <div className="d-flex flex-column gap-2">
                              <h4>Dataroom Management</h4>
                              <div className="d-flex flex-column gap-1">
                                <div className="d-flex align-items-start gap-1">
                                  <Check
                                    width={18}
                                    className="text-white flex-shrink-0"
                                  />
                                  <p>
                                    Centralize key investor documents and
                                    streamline your due diligence prep.
                                  </p>
                                </div>
                                <div className="d-flex align-items-start gap-1">
                                  <Check
                                    width={18}
                                    className="text-white flex-shrink-0"
                                  />
                                  <p>
                                    Receive one free executive summary to share
                                    with investors; additional copies cost €100
                                    each.
                                  </p>
                                </div>
                              </div>
                            </div>
                            <div className="d-flex flex-column gap-2">
                              <h4>Cap Table Management :</h4>
                              <div className="d-flex flex-column gap-1">
                                <div className="d-flex align-items-start gap-1">
                                  <Check
                                    width={18}
                                    className="text-white flex-shrink-0"
                                  />
                                  <p>Know who owns what in your company.</p>
                                </div>
                              </div>
                            </div>
                            <div className="d-flex flex-column gap-2">
                              <h4>Investor Reporting Module :</h4>
                              <div className="d-flex flex-column gap-1">
                                <div className="d-flex align-items-start gap-1">
                                  <Check
                                    width={18}
                                    className="text-white flex-shrink-0"
                                  />
                                  <p>
                                    Ensure that you connect with your investors
                                    with updates. No more ‘out of sight, out of
                                    mind’!.
                                  </p>
                                </div>
                              </div>
                            </div>
                            <div className="mt-3">
                              <button className="activate_btn border-0">
                                Activate Now
                              </button>
                            </div>
                          </div>
                        </div>
                        <div className="card_reco">
                          <p>Recommended</p>
                        </div>
                      </div>
                    </div>
                    <div className="col-md-4">
                      <div className="package_card h-100">
                        <div className="d-flex flex-column gap-0 h-100">
                          <div className="d-flex flex-column gap-2 card_top">
                            <h3>Learn To Raise</h3>
                            <ul className="d-flex flex-column gap-1">
                              <li>€1,200 Euro (one-time).</li>
                              <li>Join live investor meetings</li>
                              <li>Learn to pitch and raise capital</li>
                              <li>Access for 3 team members</li>
                              <li>Set up your company the right way</li>
                            </ul>
                          </div>
                          <div className="inner_card d-flex flex-column gap-2 h-100">
                            <span className="mainp text-white">Includes :</span>
                            <div className="d-flex flex-column justify-content-between h-100">
                              <div className="d-flex flex-column gap-2">
                                <div className="d-flex flex-column gap-2">
                                  <div className="d-flex align-items-start gap-1">
                                    <p>
                                      Launch your startup the smart way: join
                                      live investor meetings, master your pitch,
                                      and raise capital. Get access for 3 team
                                      members and set up your company for
                                      success from day one.
                                    </p>
                                  </div>
                                  <div className="d-flex align-items-start gap-1">
                                    <p>
                                      Launch your startup the smart way: join
                                      live investor meetings, master your pitch,
                                      and raise capital. Get access for 3 team
                                      members and set up your company for
                                      success from day one.
                                    </p>
                                  </div>
                                </div>
                              </div>

                              <div className="mt-3">
                                <button className="activate_btn border-0">
                                  Activate Now
                                </button>
                              </div>
                            </div>
                          </div>
                        </div>
                        <div className="card_reco">
                          <p>Optional</p>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </SectionWrapper>
            </div>
          </div>
        </div>
      </Wrapper>

      <style jsx>{`
        .subscription-header {
          display: flex;
          justify-content: space-between;
          align-items: flex-end;
          margin-bottom: 2rem;
          padding-bottom: 1rem;
          border-bottom: 1px solid #e5e7eb;
        }

        .subscription-title h1 {
          font-size: 2rem;
          font-weight: 700;
          color: #0a0a0a;
          margin: 0 0 0.5rem 0;
        }

        .subscription-title p {
          color: #6b7280;
          margin: 0;
          font-size: 1.1rem;
        }

        .subscription-count {
          background: #f8fafc;
          padding: 0.5rem 1rem;
          border-radius: 20px;
          font-size: 0.9rem;
          color: #6b7280;
        }

        .empty-state {
          text-align: center;
          padding: 4rem 2rem;
          background: #f8fafc;
          border-radius: 12px;
          margin: 2rem 0;
        }

        .empty-icon {
          color: #9ca3af;
          margin-bottom: 1.5rem;
        }

        .empty-state h3 {
          font-size: 1.5rem;
          color: #374151;
          margin: 0 0 1rem 0;
        }

        .empty-state p {
          color: #6b7280;
          margin: 0;
        }

        .subscription-grid {
          display: grid;
          grid-template-columns: repeat(auto-fill, minmax(350px, 1fr));
          gap: 1.5rem;
          margin-top: 2rem;
        }

        .subscription-card {
          background: #fff;
          border-radius: 16px;
          box-shadow: 0 4px 6px rgba(0, 0, 0, 0.05);
          overflow: hidden;
          transition: all 0.3s ease;
          border: 1px solid #f1f5f9;
        }

        .subscription-card:hover {
          transform: translateY(-4px);
          box-shadow: 0 10px 25px rgba(0, 0, 0, 0.1);
        }

        .card-header {
          display: flex;
          justify-content: space-between;
          align-items: flex-start;
          padding: 1.5rem;
          background: linear-gradient(135deg, #efefef 0%, #efefef 100%);
          border-bottom: 1px solid #e5e7eb;
        }

        .card-title-section {
          display: flex;
          align-items: center;
          gap: 0.75rem;
        }

        .card-icon {
          display: flex;
          align-items: center;
          justify-content: center;
          width: 48px;
          height: 48px;
          border-radius: 12px;
          background: linear-gradient(
            135deg,
            var(--primary) 0%,
            var(--primary-icon) 100%
          );
          color: white;
          flex-shrink: 0;
        }

        .card-header h3 {
          font-size: 1.25rem;
          font-weight: 600;
          color: #0a0a0a;
          margin: 0;
        }

        .status-badge {
          padding: 0.25rem 0.75rem;
          border-radius: 20px;
          font-size: 0.75rem;
          font-weight: 600;
        }

        .status-active {
          background: #ecfdf5;
          color: #065f46;
        }

        .status-inactive {
          background: #fef2f2;
          color: #991b1b;
        }

        .card-body {
          padding: 1.5rem;
        }

        .price-section {
          display: flex;
          align-items: baseline;
          gap: 0.25rem;
          margin-bottom: 1.5rem;
        }

        .price {
          font-size: 1.75rem;
          font-weight: 700;
          color: #0a0a0a;
        }

        .period {
          color: #6b7280;
          font-size: 0.9rem;
        }

        .details-grid {
          display: flex;
          flex-direction: column;
          gap: 0.75rem;
          margin-bottom: 1.5rem;
        }

        .detail-item {
          display: flex;
          align-items: center;
          gap: 0.5rem;
          color: #6b7280;
          font-size: 0.9rem;
        }

        .detail-item svg {
          flex-shrink: 0;
        }

        .features-section h4 {
          font-size: 1rem;
          font-weight: 600;
          color: #374151;
          margin: 0 0 1rem 0;
        }

        .features-list {
          list-style: none;
          padding: 0;
          margin: 0;
          display: flex;
          flex-direction: column;
          gap: 0.5rem;
        }

        .features-list li {
          display: flex;
          align-items: center;
          gap: 0.5rem;
          font-size: 0.9rem;
          color: #4b5563;
        }

        .features-list li svg {
          color: #10b981;
          flex-shrink: 0;
        }

        @media (max-width: 768px) {
          .subscription-header {
            flex-direction: column;
            align-items: flex-start;
            gap: 1rem;
          }

          .subscription-title h1 {
            font-size: 1.75rem;
          }

          .subscription-grid {
            grid-template-columns: 1fr;
          }

          .card-header {
            flex-direction: column;
            align-items: flex-start;
            gap: 1rem;
          }
        }
      `}</style>
    </>
  );
}
